
/*----------Main skills----------*/

.skills{
    margin: 30px 0;

}
.icons{
    display: flex;
    justify-content: space-around;
    font-size: 1.2em;
    
}
.rectangle{
    width: 90px;
}

.circle{
    width: 40px;
    height: 40px;
    border: none;
    background: rgb(178,90,236);
    background: linear-gradient(180deg, rgba(178,90,236,1) -4%, #8232d1 0%, rgba(155,71,223,1) 17%, rgba(178,90,236,1) 41%, rgba(144,119,237,1) 56%, rgba(126,135,238,1) 69%, rgba(89,167,240,1) 85%, rgba(68,185,241,1) 94%);
    padding: 10%;
    border-radius: 50%;
   margin: 25% ;
   transition: all .4s ease-in-out;
    -o-transition: all .4s ease-in-out;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
   
}
.circle:hover {
    background-position: 100% 0;
    transition: all .4s ease-in-out;
    -o-transition: all .4s ease-in-out;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
    box-shadow: 0 4px 15px 0 rgba(144,119,237,1);
}
.circle::focus {
    outline: none;
}
.second{
    display: none;
}


@media screen and (min-width:728px) {
    .second{
        display: block;
    }
}